body {
  color: $color-text;
  font-family: $font-family;
}

h1,h2,h3,h4,h5,h6,.h1,.h2,.h3,.h4,.h5,.h6 {
  margin: 0;
}


h1, .h1 {
  font-size: 3em;
  font-family: $font-family;
  font-weight: 200;
  color: beige;
  text-align: center;
}
h2, .h2 {
  font-size: 2em;
  font-family: $font-family;
  color: $white;
}
h3, .h3 {
  font-size: 1.5em;
  font-family: $font-family;
}
h4, .h4 {
  font-size: 1.5em;
  font-family: $font-family;
}
h5, .h5 {
  font-size: 1.5em;
  font-family: $font-family;
}
h6, .h6 {
  font-size: 0.8em;
  font-family: $font-family;
  font-weight: 300;
  color: orangered;
  text-transform: uppercase;
  letter-spacing: 1.0em;
}